import React, { useState, useEffect } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { ajaxRequest } from "../../services/Ajax";
import Alerta from '../Alert/Alerta';

// Validao do esquema usando Yup
const validationSchema = Yup.object().shape({
    descricao: Yup.string().required("Descrio  obrigatria"),
    valor: Yup.number()
        .required("Valor  obrigatrio")
        .positive("Valor deve ser positivo"),
    data: Yup.date().required("Data  obrigatria"),
    categoria: Yup.string().required("Categoria  obrigatria"),
    situacao: Yup.string().required("Situao  obrigatria"),
    tipoDeCaixa: Yup.string().required("Tipo de Caixa  obrigatrio"),
    tipoDeConta: Yup.string().required("Tipo de Conta  obrigatria"),
});

const FormRegistrosCotasPagar = ({ setRegistros, updateData }) => {
    const [alerta, setAlerta] = useState({ mensagem: "", tipo: "" });
    const initialValues = {
        idRegistro: "",
        descricao: "",
        valor: "",
        data: "",
        categoria: "",
        situacao: "",
        tipoDeCaixa: "",
        tipoDeConta: "",
    };
    const [tiposDeCaixa, setTiposDeCaixa] = useState([]);
    const [carregando, setCarregando] = useState(false);
    const [categoriaSelecionada, setCategoriaSelecionada] = useState(false);
    const handleSubmit = async (values, { setSubmitting }) => {
        setCarregando(true);
        const dadosJSON = JSON.stringify(values);

        try {
            const response = await ajaxRequest({ data: { qualFuncao: "salvarRegistro", dados: dadosJSON } });

            if (response.situacao) {
                setAlerta({ mensagem: response.mensagem, tipo: "success" });
                updateData();
            } else {
                setAlerta({ mensagem: response.mensagem, tipo: "danger" });
            }
        } catch (error) {
            console.error("Erro ", error.message);
            setAlerta({ mensagem: "Erro ao salvar o registro, tente novamente.", tipo: "danger" });
        } finally {
            setCarregando(false);
            setSubmitting(false);
        }
    };    
    const fetchTiposDeCategoria = async (value) => {
        setCarregando(true);
        try {
            const response = await ajaxRequest({
                data: { qualFuncao: "carregarCadastroTipoCategoria", categoria: value },
            });
            if (Array.isArray(response)) {
                setTiposDeCaixa(response);
            } else {
                console.error("A resposta no contm um array de tipos de registros", response);
            }
        } catch (error) {
            console.error("Erro na requisio", error);
        } finally {
            setCarregando(false);
        }
    };
    useEffect(() => {
        if (alerta.mensagem) {
            
            const timer = setTimeout(() => {
                setAlerta({ mensagem: "", tipo: "" }); // Limpa o alerta apos 3 segundos
            }, 3000);
            return () => clearTimeout(timer); // Limpa o timer se o componente for desmontado
        }
    }, [alerta.mensagem]);
    
    return (
        <>
            {alerta.mensagem && <Alerta mensagem={alerta.mensagem} tipo={alerta.tipo} />}
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={handleSubmit}
            >
                {({ isSubmitting, values, setFieldValue }) => (
                    <Form>
                        <div className="row">
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="idRegistro">ID Registro</label>
                                    <Field type="text" name="idRegistro" className="form-control" />
                                    <ErrorMessage name="idRegistro" component="div" className="text-danger" />
                                </div>
                            </div>
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="descricao">Descrio</label>
                                    <Field type="text" name="descricao" className="form-control" />
                                    <ErrorMessage name="descricao" component="div" className="text-danger" />
                                </div>
                            </div>
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="categoria">Categoria</label>
                                    <Field
                                        as="select"
                                        name="categoria"
                                        className="form-control"
                                        onChange={(e) => {
                                            const selectedValue = e.target.value;
                                            setFieldValue("categoria", selectedValue);
                                            setCategoriaSelecionada(!!selectedValue);
                                            fetchTiposDeCategoria(selectedValue);
                                        }}
                                    >
                                        <option value="">Selecione uma categoria</option>
                                        <option value="Sada">Sada</option>
                                    </Field>
                                    <ErrorMessage name="categoria" component="div" className="text-danger" />
                                </div>
                            </div>
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="tipoDeCaixa">Tipo de Registros</label>
                                    <Field
                                        as="select"
                                        name="tipoDeCaixa"
                                        className="form-control"
                                        disabled={!categoriaSelecionada}
                                    >
                                        <option value="">Selecione um tipo</option>
                                        {tiposDeCaixa.map((tipo) => (
                                            <option key={tipo.id} value={tipo.grupo}>
                                                {tipo.grupo}
                                            </option>
                                        ))}
                                    </Field>
                                    <ErrorMessage name="tipoDeCaixa" component="div" className="text-danger" />
                                    {carregando && (
                                        <div>
                                            Carregando...
                                            <span className="spinner-border spinner-border-sm mr-2"></span>
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="valor">R$ Valor</label>
                                    <Field type="number" name="valor" className="form-control" />
                                    <ErrorMessage name="valor" component="div" className="text-danger" />
                                </div>
                            </div>
                            <div className="col-md-3 mt-1">
                                <div className="form-group">
                                    <label htmlFor="data">Data</label>
                                    <Field type="date" name="data" className="form-control" />
                                    <ErrorMessage name="data" component="div" className="text-danger" />
                                </div>
                            </div>
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="situacao">Situao</label>
                                    <Field as="select" name="situacao" className="form-control">
                                        <option value="">Selecione uma situao</option>
                                        <option value="Pendente">Pendente</option>
                                        <option value="Pago">Pago</option>
                                        <option value="Cancelado">Cancelado</option>
                                    </Field>
                                    <ErrorMessage name="situacao" component="div" className="text-danger" />
                                </div>
                            </div>
                            <div className="col-md-2 mt-1">
                                <div className="form-group">
                                    <label htmlFor="tipoDeConta">Tipo de Conta</label>
                                    <Field as="select" name="tipoDeConta" className="form-control">
                                        <option value="">Selecione um tipo</option>
                                        <option value="Caixa">Caixa</option>
                                        <option value="Banco">Banco</option>
                                    </Field>
                                    <ErrorMessage name="tipoDeConta" component="div" className="text-danger" />
                                </div>
                            </div>
                        </div>

                        <div className="row p-3">
                            <div className="col-md-6">
                                <button
                                    type="submit"
                                    className="btn btn-primary"
                                    disabled={isSubmitting}
                                >
                                    {isSubmitting ? (
                                        <>
                                            <span className="spinner-border spinner-border-sm mr-2"></span>
                                            Enviando...
                                        </>
                                    ) : (
                                        "Salvar"
                                    )}
                                </button>
                            </div>
                        </div>
                    </Form>
                )}
            </Formik>
        </>
    );
};

export default FormRegistrosCotasPagar;
